import { useQuery } from "react-query";
import YouTube from "react-youtube";
import styled from "styled-components";
import { getVideo, INetFlix } from "../api";
import { makeImagePath } from "../utils";

const BigCover = styled.div`
  width: 100%;
  background-size: cover;
  background-position: center center;
  height: 500px;
`;

const BigTitle = styled.h3`
  color: ${(props) => props.theme.white.lighter};
  padding: 20px;
  font-size: 35px;
  position: relative;
  top: -80px;
`;

const BigOverview = styled.p`
  padding: 20px;
  position: relative;
  top: -80px;
  line-height: 30px;
  color: ${(props) => props.theme.white.lighter};
`;

interface IPopUp {
    cat: string;
    clickedMovie : INetFlix;
}

export default function PopUp(data : IPopUp){
    const {data:videoData, isLoading:vedioLoading} = useQuery(["video"], () => getVideo(data.cat, data.clickedMovie.id));
    
    return(
            <>
                {videoData?.results.length == 0? 
                    <BigCover style={{ backgroundImage: `linear-gradient(to top, black, transparent), url(${makeImagePath(data.clickedMovie.backdrop_path, "w500")})` }}/> :
                    <BigCover>
                    <YouTube
                        videoId={videoData?.results[0].key}
                        opts={{
                            width: "100%",
                            height: "500",
                            playerVars: {
                                autoplay: 1, 
                                rel: 0, 
                                modestbranding: 1, 
                            }
                        }}
                        onEnd={(e)=>{e.target.stopVideo(0);}}      
                        />
                </BigCover>
                }
                <BigTitle>{data.clickedMovie.title? data.clickedMovie.title : data.clickedMovie.name}</BigTitle>
                <BigOverview>asdf</BigOverview>
            </>
    );
}